@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap&subset=cyrillic-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "RoubleArial";
  src: url("data:font/truetype;base64,") format("truetype");
  font-weight: normal;
}
@font-face {
  font-family: "RoubleArial";
  src: url("data:font/truetype;base64,") format("truetype");
  font-weight: bold;
}
.rouble {
  font-family: "RoubleArial";
  font-weight: normal;
  font-style: normal;
  margin-left: -0.2em;
}

.App {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  flex-flow: column;
  margin: 15px 0;
}

.App-title {
  color: #0E316A;
  text-transform: uppercase;
  font-weight: 900;
}

.app-body {
  margin: 0 auto;
  width: 100%;
  max-width: 830px;
  color: #0E316A;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  border: 1px solid #A4B2C0;
}
.app-body .investment-summ, .app-body .investment-period, .app-body .investment-adding, .app-body .result {
  display: inline-block;
}
.app-body .investment-summ, .app-body .investment-period {
  width: 60%;
}
.app-body .investment-adding, .app-body .result {
  width: 40%;
}
@media (max-width: 991px) {
  .app-body .investment-summ, .app-body .investment-period, .app-body .investment-adding, .app-body .result {
    width: 50%;
  }
}
@media (max-width: 767px) {
  .app-body .investment-summ, .app-body .investment-period, .app-body .investment-adding, .app-body .result {
    width: 100%;
  }
  .app-body .investment-summ {
    order: -2;
  }
  .app-body .investment-period {
    order: -1;
  }
}

.size12 {
  font-size: 12px;
}

.size14 {
  font-size: 14px;
}

.size16 {
  font-size: 16px;
}

.size18 {
  font-size: 18px;
}

.size22 {
  font-size: 22px;
}

.w-40 {
  width: 40%;
}

.w-60 {
  width: 60%;
}
.investment-summ {
  background-color: #DAE1E7;
  display: inline-flex !important;
  flex-flow: column;
  justify-content: flex-end;
}

.investment-summ__summ {
  width: 200px;
  text-align: center;
  font-size: 24px;
}
.investment-summ__summ:focus {
  outline: none;
}
@media (max-width: 991px) {
  .investment-summ__summ {
    width: 100px;
    font-size: 18px;
  }
}

.range {
  position: relative;
}

input[type=range] {
  width: 100%;
  z-index: 1;
  position: absolute;
  opacity: 0;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  cursor: pointer;
}

input[type=range]::-webkit-slider-thumb {
  height: 20px;
  width: 30px;
  opacity: 1;
  margin-top: -9px;
  margin-left: 5px;
  -webkit-appearance: none;
}

.sliderticks {
  position: relative;
  height: 30px;
  top: 10px;
}

.sliderticks .rangeLink {
  display: inline-flex;
  position: absolute;
  justify-content: center;
  text-align: center;
  width: 1px;
  background: #000;
  height: 10px;
  line-height: 40px;
  margin: 0 0 20px 0;
  cursor: pointer;
}

.range__thumb {
  position: absolute;
  width: 20px;
  height: 20px;
  background: #fff;
  top: -9px;
  border-radius: 50%;
  border: 1px solid #cccccc;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.range__total {
  position: absolute;
  height: 3px;
  width: 100%;
  background: #6E7E93;
  top: 0;
}

.range__progress {
  position: absolute;
  height: 3px;
  width: 208px;
  background: #0093FF;
  top: 0;
}
.investment-period {
  background-color: #F7F7F7;
}

.investment-period__label {
  cursor: pointer;
}

.investment-period__input:checked + .investment-period__label {
  background-color: #0093FF !important;
  border-color: #0093FF !important;
  color: #fff;
}
.value-container {
  height: 200px;
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
}

.invest-value {
  background-color: #DAE1E7;
  min-height: 10px;
  width: 100%;
  flex-shrink: 0;
}

.adding-value {
  background-color: #0093FF;
  width: 100%;
  flex-shrink: 0;
}
.result {
  text-align: center;
  padding: 40px 0 80px;
}

.result__profit {
  font-size: 50px;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .result__profit {
    font-size: 40px;
  }
}

.result__btn {
  background-color: #FFC400 !important;
  color: #453302 !important;
  border: none !important;
  text-transform: uppercase;
  font-size: 14px;
  width: 200px;
}
